import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ThirdSlickItem from "./ThirdSlickItem";

export default function ThirdSlick() {
    const [width, setWidth] = useState(window.innerWidth)
    window.addEventListener('resize', ()=> setWidth(window.innerWidth))
    const settings = {
      dots: width < 900 ? true : false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
  
    return (
      <Slider {...settings}>
        <ThirdSlickItem />
        <ThirdSlickItem />
        <ThirdSlickItem />
        <ThirdSlickItem />        
      </Slider>
    );
  }