import React from "react";
import { CssBaseline, ThemeProvider, createTheme } from "@mui/material";
import Landing from "./pages/Landing";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Barlow from './font/Barlow,Inter/Barlow/Barlow-Bold.ttf'

const theme = createTheme({
  palette: {
    oq: {
      light: 'rgba(145, 158, 171, 0.24)',
      main: "#fff",
    },
    primary:{
      light: '#B3200E',
      main: '#fa541c'
    },
    secondary: {
      main: '#919eab',
      dark: '#212b36'
    }
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <CssBaseline />
        <BrowserRouter>
          <Routes>
            <Route path="/" element={<Landing />} />
          </Routes>
        </BrowserRouter>
      </div>
    </ThemeProvider>
  );
}

export default App;
