import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SecondSlickItem from "./SecondSlickItem";


export default function SecondSlick() {
    const [width, setWidth] = useState(window.innerWidth)
    window.addEventListener('resize', ()=> setWidth(window.innerWidth))
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: width>1200 ? 3: (width<1200 && width>900) ? 2 : (width<900 && width>600) ? 2 : 1,
      slidesToScroll: 1,
    };
  
    return (
      <Slider {...settings}>
        <SecondSlickItem />
        <SecondSlickItem />
        <SecondSlickItem />
        <SecondSlickItem />
        <SecondSlickItem />
        <SecondSlickItem />
      </Slider>
    );
  }
  