import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SecondSlickItem from "./SecondSlickItem";
import LogoSliderItem from "./LogoSliderItem";


export default function LogoSlider() {
    const [width, setWidth] = useState(window.innerWidth)
    window.addEventListener('resize', ()=> setWidth(window.innerWidth))
    const settings = {
      dots: false,
      infinite: true,
      autoplay: 'true',
      autoplaySped: 1500,
      speed: 2000,
      slidesToShow: width>1200 ? 6: (width<1200 && width>900) ? 5 : (width<900 && width>600) ? 3 : 2,
      slidesToScroll: 1,
    };
  
    return (
      <Slider {...settings}>
        <LogoSliderItem />

        <LogoSliderItem />
        <LogoSliderItem />

        <LogoSliderItem />
        <LogoSliderItem />
        <LogoSliderItem />
        <LogoSliderItem />
        <LogoSliderItem />
        <LogoSliderItem />

      </Slider>
    );
  }
  